.step3-form{display:flex;flex-direction:column;gap:1.6rem}.add-on{display:flex;align-items:center;border:1px solid var(--gray);border-radius:.8rem;padding:1.8rem 2.4rem;cursor:pointer;transition:border-color .3s ease;flex-wrap:nowrap}.add-on:hover{border-color:var(--purple)}@media(max-width: 768px){.add-on{padding:1.1rem 1.6rem}}.add-on.selected{border-color:var(--purple);background-color:var(--very-light-gray)}.add-on input[type=checkbox]{appearance:none;width:2rem;height:2rem;min-width:2rem;min-height:2rem;margin-right:2.4rem;border:2px solid var(--gray);border-radius:.4rem;background-color:rgba(0,0,0,0);cursor:pointer;flex-shrink:0;transition:background-color .2s ease,border-color .2s ease}@media(max-width: 768px){.add-on input[type=checkbox]{margin-right:1.6rem}}.add-on input[type=checkbox]:checked{background-color:var(--purple);border-color:var(--purple);position:relative}.add-on input[type=checkbox]:checked::after{content:"✔";color:var(--white);font-size:1.2rem;font-weight:bold;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.add-on-content{display:flex;flex-direction:column;flex-grow:1;gap:.7rem}.add-on-label{font-weight:500;font-size:1.6rem;color:var(--dark-blue)}@media(max-width: 768px){.add-on-label{font-size:1.4rem}}.add-on-description{font-size:1.4rem;color:var(--dark-gray)}@media(max-width: 768px){.add-on-description{font-size:1.2rem}}.add-on-price{font-size:1.2rem;color:var(--purple)}